import Home from './Home/Home';
import Portfolio from './Portfolio/Portfolio';
import About from './About/AboutMe';

export default function Main() {
    const style = {
        height : 'calc(100% - 48px)'
    }
    return (
        <main className='overflow-y-auto' style={style}>
            <Home />
            <Portfolio />
            <About />
        </main>
    )
}