import Bg from '../../../media/bg_home.jpg';

export default function Home() {
    let style = {
        backgroundImage : `url(${Bg})`,
        backgroundRepeat : `no-repeat`,
        backgroundSize : 'cover',
        backgroundPosition : `center`
    }

    let text = {
        WebkitTextStroke : `4px #0000`
    }

    let handleClick = (e) => {
        e.preventDefault();
    }

    return (
        <section className='w-full h-full flex justify-center items-center flex-col' style={style}>
            <h1 className='text-white text-5xl lg:text-7xl xl:text-9xl font-Archivo' style={text}>
                WELCOME
            </h1>
            <article>
                <button onClick={handleClick} className='px-6 py-3 rounded-xl mt-6 bg-white transform hover:scale-110 hover:text-white hover:bg-color-third transition-all font-Roboto lg:text-xl xl:text-3xl'>
                    Portfolio
                </button>
            </article>
        </section>
    )
}
