import React, { Component } from 'react';
import NavbarItems from './NavbarItems';

export default class Navbar extends Component {
    constructor(props){
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }
    
    handleClick(e){
        e.preventDefault();
    }

    render() {
        return (
            <nav className='w-11/12 xl:w-10/12 2xl:container mx-auto h-full'>
                <ul className='flex justify-around items-center h-full md:w-8/12 lg:w-3/6'>
                {NavbarItems.map((items, index) => {    
                    return (
                        <li onClick={this.handleClick} key= {index} className={items.listClass}>
                            <a href={items.url} className={items.urlClass}>
                                {items.title}
                            </a>
                            <span className={items.iconClass}>{items.iconName}</span>
                        </li>
                    )
                })}
                </ul>
            </nav>
        )
    }
}