import ChartList from './ChartLIst';

export default function Chart() {
    let handleClick = (e) => {
        e.preventDefault()
    }

    return (
        <article className='w-11/12 xl:w-10/12 2xl:container mx-auto flex flex-wrap justify-center'>
            {ChartList.map((items, index) => {
                return (
                    <section
                        key={index}
                        className="box-border relative mx-4 my-4 2xl:mx-5 2xl:my-0 h-96 max-w-xs overflow-hidden">
                        <header className="py-3 flex justify-between px-5 bg-gray-200">
                            <h1 className="font-bold"> {items.title} </h1>
                            <span
                                onClick={handleClick}
                                className="material-icons hover:text-gray-700 cursor-pointer transition-all">
                                visibility
                            </span>
                        </header>
                        <article className="bg-gray-100">
                            <section>
                                <img src={items.imgCover} alt="sample website"></img>
                            </section>
                            <section className="m-5">
                                <h4 className="font-bold text-base my-3">
                                    Diskripsi
                                </h4>
                                <p className="break-all">
                                    {items.diskrip}
                                </p>
                            </section>
                            <section className="w-full p-1 absolute bg-gray-200 bottom-0 text-center">
                                <button
                                    onClick={handleClick}
                                    className="px-4 py-2 hover:bg-blue-500 rounded-xl text-xs transition-all font-bold">
                                    Detail
                                </button>
                            </section>
                        </article>
                    </section>
                )
            })}
        </article>

    )
}
