import Profile from './AboutList/Profile';
import Education from './AboutList/Education';
import Skill from './AboutList/Skill';

export default function AboutMe() {
    return (
        <section>
            <h1 className='font-bold text-center my-7 text-3xl lg:text-4xl xl:text-6xl'>About Me</h1>
            <article className='w-11/12 xl:w-10/12 2xl:container mx-auto'>
                <Profile />
                <Education />
                <Skill />
            </article>
        </section>
    )
}